.pagination-list {
  justify-content: center;
  gap: 20px;
  margin: 0;
  padding: 0;
  font-weight: bold;
  list-style-type: none;
  display: flex;
}

.pagination-list li a, .pagination-list li span {
  color: #000;
  -webkit-user-select: none;
  user-select: none;
  background: #fff;
  border: 1px solid #000;
  border-radius: 20px;
  padding: 10px 15px;
  text-decoration: none;
  display: block;
}

.pagination-list .previous a {
  color: #fff;
  background: #000;
  border: 1px solid #000;
}

.pagination-list .next a {
  color: #000;
  background: #eac645;
  border: 1px solid #000;
}

.pagination-list .current span {
  color: #fff;
  background: #000;
  border: 1px solid #000;
}

.pagination-list .previous span, .pagination-list .next span {
  color: #d6d9dd;
  background: #bdc2c8;
  border: 1px solid #bdc2c8;
}

